      -ms-transform: scale(1.135);
          transform: scale(1.135);
}

.hirebtn {
  padding: 5px;
  border: 1px solid white;
  border-radius: 7px;
}

.hirebtn:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
